import "core-js/modules/es.array.push.js";
import "vant/es/toast/style";
import router from "@/router";
import { ref } from "@vue/reactivity";
import { Toast } from "vant";
import { useStore } from "vuex";
export default {
  setup() {
    // 返回上一层
    let goBack = () => {
      router.go(-1);
    };
    // 随机验证码
    let str = "";
    // 手机号
    let phone = ref("");
    // 验证码
    let ranNum = ref("");

    // 清除
    let clear = () => {
      phone.value = "";
    };
    // 密码
    let password = ref();
    // 获取验证码
    let goNum = () => {
      let reg = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
      if (!reg.test(phone._value)) {
        Toast("请输入正确手机号");
      } else {
        // 随机4位验证码
        let str1 = "";
        for (let i = 0; i < 4; i++) {
          str1 += parseInt(Math.random() * 10);
        }
        str = str1;
        console.log(str);
        Toast("验证码是：" + str);
      }
    };
    // 注册
    let goSet = () => {
      if (ranNum.value == str) {
        console.log("成功");
        window.sessionStorage.setItem("data", JSON.stringify({
          phone: phone,
          password: password
        }));
        console.log(password.value);
        Toast("注册成功");
        setTimeout(function () {
          router.push({
            path: "/login"
          });
        }, 200);
      } else {
        console.log("失败");
      }
    };
    let showeye = ref("false");
    let change = () => {
      showeye.value = !showeye.value;
    };
    let store = useStore();
    console.log(store);
    return {
      goBack,
      goNum,
      phone,
      ranNum,
      password,
      clear,
      goSet,
      change,
      showeye
    };
  }
};